*{
    &::-webkit-scrollbar{
        display: none;
    }
}


/* app.css */

/* General body styling */
body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-size: cover;
    background-position: center;
    height: 100vh;
    overflow: hidden;
  }
  
/* Styles for background wrapper */
.background-wrapper {
    background-image: url('../backimg.jpg'); /* Update path if needed */
    background-size: cover;
    background-position: center;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  
  /* Styles for container */
  .container {
    background: rgba(255, 255, 255, 0.9); /* Semi-transparent white background */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    max-width: 800px; /* Adjust as needed */
    width: 100%;
    height: 100%;
  }
  

/* Popup container styles */
/* app.css */


.popup-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  }
  
  /* Popup content styles */
  .popup-content {
    background: #fff;
    padding:60px 40px;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    width: 80%;
    text-align: center;
    max-width: 400px; /* Adjust as needed */
    position: relative;
    overflow: hidden;
    border: 2px solid #ddd; /* Base border color */
    animation: glowBorder 1.5s infinite alternate;
  }
  
  /* Glowing border keyframes */
  @keyframes glowBorder {
    from {
      border-color: #ddd;
      box-shadow: 0 0 5px rgba(0, 98, 255, 0.5);
    }
    to {
      border-color: rgb(0, 106, 255);
      box-shadow: 0 0 15px rgba(0, 98, 255, 1);
    }
  }
  
  
  
  .popup-content h1{
    font-size: 24px;
    font-weight: 500;
  }
  .popup-content p{
    color: #888;
  }
  /* Sign-in button styling */
  .popup-content .signin-button {
    background: #4285f4;
    color: white;
    border: none;
    border-radius: 8px;
    padding: 12px 24px;
    font-size: 16px;
    cursor: pointer;
    margin-top: 50px;
    width: 100%;
    transition: background 0.3s ease;
  }
  
  .popup-content .signin-button:hover {
    background: #357ae8;
  }
